button {
  border: 0;
  background-color: inherit;
  //   color: whitesmoke;
  //   padding: 5px 10px;
  //   margin: 5px;
  cursor: pointer;
}

form > * {
  display: block;
  width: 200px;
  margin: 10px 0px;
}

.btn-link {
  border: 0;
  background-color: transparent;
  text-decoration: underline;
  transition: 0.5s;
}
.btn-link:hover {
  background-color: $clr1;
  transform: scale(1.1);
}
