.task-title-textarea {
  transition-duration: 85ms;
  transition-property: background-color, border-color, box-shadow;
  transition-timing-function: ease;
  outline: none;
  box-sizing: border-box;
  display: block;
  border: none;
  -webkit-appearance: none;
  width: 90%;
  line-height: 24px;
  margin: -4px -8px;
  min-height: 24px;
  padding: 4px 8px;
  resize: none;
  background: #0000;
  border-radius: 3px;
  box-shadow: none;
  font-size: 20px;
  font-weight: 600;
  overflow: hidden;
  overflow-wrap: break-word;
  height: 33px;
}
.window-header {
  min-height: 32px;
  margin: 12px 40px 8px 20px;
  position: relative;
  z-index: 1;
}
.icon-title {
  font-size: 16px;
  height: 24px;
  line-height: 32px;
  width: 32px;
}
.icon-description {
  width: 32px;
  font-size: 20px;
}
.description-title {
  display: inline-block;
  margin: 0;
  min-height: 18px;
  min-width: 40px;
  width: auto;
}
.-container {
  margin: 0 0 4px 20px;
  padding: 8px 0;
  position: relative;
}

.main-content-text-area {
  cursor: pointer;
  background-color: #091e420a;
  border: none;
  width: 95%;
  margin-left: 70px;
  border-radius: 4px;
  box-shadow: none;
  color: var(--ds-text, inherit);
  display: block;
  min-height: 40px;
  padding: 8px 12px;
  text-decoration: none;
  resize: none;
}

.main-content-text-area:hover {
  background-color: #091e4214;
}

.details-grid {
  display: grid;
  grid-template-columns: 50px 1fr;
  margin: 0 0 4px 20px;
  padding: 24px 0;
  position: relative;
}

.activity-head-container {
  margin: 0 0 4px 20px;
  padding: 8px 0;
  position: relative;
}
.grid-all {
  display: grid;
  grid-template-columns: 533px 260px;
}
.task-sidebar {
  justify-content: center;
  align-items: center;
  display: flex;
}
.attachment-img {
  width: 112px;
  height: 80px;
  border-radius: 3px;
  object-fit: contain;
}
.title-icon-container {
  display: flex;
  align-items: center;
}
.task-title-textarea:focus {
  box-shadow: inset 0 0 0 2px #0079bf;
  background-color: #fff;
}
.quill {
  width: 95%;
  height: 223px;
  margin-left: 70px;
  background-color: #fff;
}
.quill-container .ql-editor:focus {
  outline: none;
}

.quill-container:focus-within {
  box-shadow: inset 0 0 0 2px #0079bf;
}
.quill-save-btn {
  padding: 6px 12px;
  background-color: #0c66e4;
  color: white;
  border-radius: 3px;
  line-height: 20px;
  margin-inline-end: 4px;
}
.quill-save-btn:hover {
  background-color: $btn-hovered;
}
.quill-cancel-btn {
  padding: 6px 12px;

  border-radius: 3px;
  line-height: 20px;
  margin-inline-end: 4px;
}
.quill-btns-container {
  margin-top: 8px;
  margin-left: 70px;
}
.activity-comments {
  margin-top: 8px;
}
.task-sidebar {
  font-weight: 400;
}
.date-wrapper {
  display: flex;
  align-items: center;
}

.date-wrapper input {
  border: inset 0 0 0 2px var(--ds-text, #a3a3a3);
  flex-shrink: 0;
  height: 16px;
  overflow: hidden;
  position: relative;
  transition: all 0.2s ease-in-out;
  white-space: nowrap;
  width: 16px;
  cursor: pointer;
  background-color: var(--ds-background-selected-bold, #0079bf);
}

.dates-container {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Noto Sans', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
}
.date-completed {
  font-size: 12px;
  line-height: 16px;
  padding: 0 4px;

  border-radius: 2px;
  margin: auto 0 auto 8px;
  color: #ffffff;
  background-color: #1f845a;
}
.arrow-down-icon {
  margin-left: 4px;
  width: 16px;
  height: 16px;
  font-size: 16px;
  text-align: center;
  align-self: center;
  display: flex;
}
.description-edit-btn {
  margin-left: 7px;
}
.attachment-btn-container {
  display: flex;
  margin-bottom: 8px;
  white-space: nowrap;
}
.attachment-button {
  text-decoration: underline;
  font-size: 14px;
  color: #000000a3;
}
.attachment-button:hover {
  background-color: inherit;
}
.attachment-date {
  color: #000000a3;
  font-size: 14px;
}
.attachment-detail-container {
  margin-left: 16px;
}
.attachment-thumbnail-name {
  font-weight: 700;
}
.attachment-container {
  margin-left: 70px;
  min-height: 84px;
  margin-bottom: 8px;
  width: 95%;
}
.attachment-container:hover {
  background-color: $bgcolor-hover-color;
  border-radius: 3px;
}
.cover-color-div {
  margin: 0;
  border-radius: 3px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: none;
  width: 50px;
  height: 32px;
}
.cover-colors-container {
  webkit-overflow-scrolling: touch;
  -webkit-transform: translate3d(0, 0, 0);
  scrollbar-color: var(--ds-background-neutral-hovered, rgba(9, 30, 66, 0.25)) var(--ds-background-neutral, rgba(255, 255, 255, 0.5));
  scrollbar-width: thin;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  padding: 4px;
  margin: 0 -4px;
  overflow-x: hidden;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.cover-color-div:hover {
  filter: brightness(80%);
}
.pop-over-content {
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0 12px 12px;
}
.members-wrapper h5 {
  margin: 0 8px 4px 0;
}
.labels-wrapper h5 {
  margin: 0 8px 4px 0;
}
.dates-wrapper h5 {
  margin: 0 8px 4px 0;
}
.color-container {
  position: relative;
  box-sizing: border-box;
  border-radius: 3px;
  border: 2px px transparent solid;
  height: 32px;
}
.color-container:hover {
  filter: brightness(80%);
}
.label-colors-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 4px;
  margin-bottom: 8px;
}
.remove-color-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.delete-btn {
  background-color: #ca3521 !important;
}
.edit-label-text {
  color: var(--ds-text-subtle, #5e6c84);
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
}
.current-label-container {
  margin: 0 -12px;
  padding: 32px;
  background-color: var(--ds-surface-sunken, #f4f5f7);
}
.current-label {
  display: inline-block;
  position: relative;
  margin-bottom: 0;
  border-radius: 3px;
  padding: 0 12px;
  max-width: 100%;
  min-width: 48px;
  height: 32px;
  box-sizing: border-box;
  background-color: var(--expanded-background-color, var(--ds-skeleton, #dfe1e6));
  line-height: 32px;
  color: var(--text-color, var(--ds-text, #172b4d));
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}

