.task-attachments {
  .attachment-header {
    margin: 0 0 4px 20px;
    padding: 8px 0;
    position: relative;

    .attachment-icon {
      font-size: 20px;
      height: 24px;
      line-height: 32px;
      width: 32px;
    }

    .attachment-title {
      font-size: 24px;
      margin-bottom: 5px;
      display: flex;
      align-items: center;
    }
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  li {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin-bottom: 8px;
  }

  p {
    font-size: 14px;
    color: #6b6b6b;
  }
}
.icon svg {
  font-size: 20px;
  width: 32px;
}
.align-text {
  display: flex;
  align-items: center;
}
